import { defineStore } from "pinia";

export const useDateStore = defineStore('date', {
    state: () => {
        let date = localStorage.getItem('date');
        if (date) {
            // date = date
        } else {
            date = { fa: '', en: '' };
        }
        return {
            date
        }
    },
    getters: {
        getDate(state) {
            return state.date
        },
    },
    actions: {
        setDate(date) {
            this.date = date
            localStorage.setItem('date', JSON.stringify(this.getDate));
        }
    }
})